import 'typeface-roboto'
import axios from 'axios'
import React, { useState } from 'react'
import {
	Button,
	CircularProgress,
	createMuiTheme,
	Paper,
	TextField,
	ThemeProvider,
	Typography,
	useMediaQuery,
} from '@material-ui/core'

import './App.css'
import Episode from './Episode'
import { useStyles } from './theme'

const App = () => {
	const classes = useStyles()

	const inputHandler = ({ target: { value } }) => setUrl(value)

	const submitHandler = async event => {
		event.preventDefault()
		setLoading(true)

		if (url === '') {
			setInfo({ name: 'No URL entered', links: [] })
			setLoading(false)
			return
		}

		try {
			let newEpisodes = []
			setEpisodes(newEpisodes)
			setInfo(null)
			const res = await axios.post(
				'/info',
				{ url },
				{ headers: { 'Content-Type': 'application/json' } }
			)
			setInfo(res.data)
			for (let index = res.data.start; index <= res.data.end; index++)
				newEpisodes.push(index)
			setEpisodes(newEpisodes)
		} catch (err) {
			console.error(err)
			setInfo({ name: 'Generation Failed', episodes: [] })
		}
		setLoading(false)
	}

	const [url, setUrl] = useState('')

	const [episodes, setEpisodes] = useState([])

	const [info, setInfo] = useState(null)

	const [loading, setLoading] = useState(false)

	return (
		<ThemeProvider theme={theme}>
			<Paper
				elevation={0}
				className={
					useMediaQuery('(min-width:720px)')
						? classes.root + ' ' + classes.rootBig
						: classes.root
				}
			>
				<header>
					<Typography
						variant={useMediaQuery('(min-width:720px)') ? 'h1' : 'h3'}
						className={classes.title}
					>
						AnimeYoutube
						<br />
						Downloader
					</Typography>
					<form className={classes.form} onSubmit={submitHandler}>
						<TextField
							autoFocus
							helperText='Enter URL of anime from animeyoutube.com or www1.kiss-anime.website'
							onChange={inputHandler}
							placeholder='URL of anime'
							value={url}
						/>
						<div className={classes.wrapper}>
							<Button disabled={loading} type='submit'>
								Start
							</Button>
							{loading && (
								<CircularProgress
									size={24}
									className={classes.buttonProgress}
								/>
							)}
						</div>
					</form>
				</header>
				<main className={classes.main}>
					{info !== null ? (
						<>
							{info.img ? (
								<img
									alt={info.name + ' poster'}
									className={classes.img}
									src={info.img}
								/>
							) : (
								<></>
							)}
							<Typography component='h2' variant='h4'>
								{info.name}
							</Typography>
						</>
					) : (
						<></>
					)}
					<div className={classes.linkList}>
						{episodes.map(no => (
							<Episode key={no} no={no} url={url} />
						))}
					</div>
				</main>
			</Paper>
		</ThemeProvider>
	)
}

const theme = createMuiTheme({
	palette: {
		type: 'dark',
	},
})

export default App
